#ourMenu {
  display: flex;
  flex-direction: column;
  background-image: url("../img/backgrounds/bg_4.jpg");
  padding-top: 10%;
  > :first-child {
    text-align: center;
    color: white;
    height: 10%;
  }
  > :nth-child(2) {
    margin-top: 2%;
    height: 10%;
    display: flex;
    flex-direction: row;
    width: 20%;
    min-height: 20px;
    justify-content: space-between;
    align-items: center;

    > :nth-child(1) {
      border-bottom: 1px solid #fac565;
      height: 1px;
      width: 30%;
      margin: 0;

    }
    > :nth-child(2) {
      background-color: #fac565;
      width: 1vw;
      height: 1vw;
      rotate: 45deg;
      margin: 0;
    }
    > :nth-child(3) {
      background-color: #fac565;
      width: 2vw;
      height: 2vw;
      rotate: 45deg;
      margin: 0;
    }
    > :nth-child(4) {
      background-color: #fac565;
      width: 1vw;
      height: 1vw;
      rotate: 45deg;
      margin: 0;
    }
    > :nth-child(5) {
      border-bottom: 1px solid #fac565;
      height: 1px;
      width: 30%;
      margin: 0;
    }
  }

  > :nth-child(3) {
    color: #444343;
    width: 50%;
    height: 10%;

    text-align: center;
    margin-top: 4%;
    margin-bottom: 4%;
  }

  > section {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: 65%;

    > div {
      margin: 0 0 4% 0;
      display: flex;
      flex-direction: row;
      width: 50%;
      justify-content: center;


      > img {
        width: 8%;
        border-radius: 100%;
        margin: 0;
      }

      > div {
        display: flex;
        flex-direction: column;
        margin: 0;

        > :first-child {
          display: flex;
          flex-direction: row;
          border-bottom: 1px #fac565 dashed;
          width: 100%;

          > :first-child {
            width: 90%;
            margin: 0;
            color: white;
          }

          > :last-child {
            width: 10%;
            margin: 0;
            color: #fac565;
          }
          }

        > :last-child {
          color: #444343;
        }
      }














    }
  }





}