#addToCard {
  background-image: url("../img/backgrounds/bg_4.jpg");
  display: flex;
  flex-direction: row;
  > img {
    width: 40%;

  }
  > div {
    display: flex;
    flex-direction: column;
    width: 60%;
    > :first-child {
      display: flex;
      flex-direction: row;
      width: 50%;
      margin: 0 0 4% 0;
      > button {
        width: 20%;
      }
    }
    > :last-child {
      display: flex;
      flex-direction: row;
      width: 100%;
      > div {
        display: flex;
        flex-direction: column;
        width: 30%;
        align-items: center;
        > img {
          width: 80%;
          border-radius: 100%;
        }
        :nth-child(2) {
          color: white;
        }

        :nth-child(3) {
          color: #444343;
        }
        > span {
          color: #fac565;
        }
      }
    }
  }
}