@media screen and (max-width: 1150px) {
  #hotPizza {
    > :nth-child(3) {
      > :first-child {
        flex-wrap: wrap;
        > div {
          width: 50%;
          height: 50%;
        }
      }

      > :last-child {
        flex-wrap: wrap;
        > div {
          width: 50%;
          height: 50%;
        }
      }
    }
  }
}

@media screen and (max-width: 900px) {
  #addToCard {
    > img {
      display: none;
    }
    > div {
      width: 100%;
    }
  }
  #ourMenu {
    > section {
      flex-wrap: wrap;
      > div {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 700px) {
  header {
    nav {
      :nth-child(1) {
        display: none;
      }
    }
  }
  
}

@media screen and (max-width: 650px) {
  #hotPizza {
    height: 1600px;
    > :nth-child(3) {
      > :first-child {
        flex-wrap: wrap;
        > div {
          width: 100%;
          height: 33.33%;
        }
        > :nth-child(2) {
          flex-direction: row-reverse;
        }
      }

      > :last-child {
        flex-wrap: wrap;
        > div {
          width: 100%;
          height: 33.33%;
        }
        > :nth-child(2) {
          flex-direction: row-reverse;
        }
      }
    }
  }

  #mapAndForm {
    flex-wrap: wrap;
    iframe {
      width: 100%;
      height: 50%;
      min-height: 300px;
    }
    form {
      width: 100%;
      height: 50%;
      min-height: 300px;
    }
  }

  footer {
    > section {
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
      > :nth-child(1) {
        width: 100%;
        height: 25%;
      }

      > :nth-child(2) {
        width: 100%;
        height: 25%;
      }

      > :nth-child(3) {
        width: 100%;
        height: 25%;
        flex-direction: row;
        flex-wrap: wrap;

        :first-child {
          width: 100%;
          margin: 0;
          padding: 0;
        }
        button {
          margin: 0;
          padding: 0;
          width: 25%;
        }
      }

      > :nth-child(4) {
        width: 100%;
        height: 25%;
      }
    }
  }

  #ourServices {
    > :last-child {
      flex-wrap: wrap;
      > div {
        width: 100%;
      }
    }
  }
}

