#mapAndForm {
  display: flex;
  flex-direction: row;
  iframe {
    width: 50%;
    border: 0;
    loading: lazy;
    referrerpolicy: no-referrer-when-downgrade;
  }
  form {
    display: flex;
    flex-direction: column;
    background-color: black;
    width: 30%;
    color: white;
    justify-content: flex-start;
    margin: 0;
    padding: 8% 16% 8% 4%;
    height: 65%;
    :first-child {
      margin: 0;
    }
    div {
      margin: 0;
      padding-bottom: 4%;
      display: flex;
      flex-direction: column;
      input {
        margin: 0;
        background-color: transparent;
        border: 0;
        border-bottom: 1px solid #444343;
      }

      input:hover {
        transition: 0.3s;
        scale: 1.05;
      }
    }
    button {
      width: 20%;
      height: 10%;
      margin: 0;
    }
  }
}