#gallery {
  display: flex;
  flex-direction: column;
  margin: 0;

  > :first-child {
    display: flex;
    flex-direction: row;
    height: 50%;
    width: 100%;
    margin: 0;
    img {
      width: 25%;
    }

  }
  > :last-child {
    background-image: url("../img/photo_gallery/bar_girl.jpg");
    background-position: 0 20%;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 50%;
    > div {
      display: flex;
      flex-direction: row;
      width: 60%;
      height: 100%;
      > div {
        width: 25%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: 0;
        > div {
          width: 50%;
          padding: 4%;
          border: 1px solid #fac565;
          margin: 0;
          img {
            background-color: #fac565;
            border-radius: 100%;
            width: 100%;
            margin: 0;

          }
        }

        > span {
          color: #fac565;
          margin: 0;
        }

        > p {
          color: #e7e3e3;
          margin: 0;
        }

      }
    }
  }
}